<template>
	<view class="diy_article">
		<uni-list-chat v-for="(o, i) in list" :key="i" :title="o[vm.title]" :to="'/pages/article/details?' + vm.article_id + '=' + o[vm.article_id]" :note="o[vm.description]"
		 :avatar="o[vm.img]">
			<view class="chat-custom-right">
				<text class="time">{{ o[vm.create_time] }}</text>
				<view class="doc"><text class="praise">{{ o[vm.praise] }}</text><text class="see">{{ o[vm.see] }}</text></view>
			</view>
		</uni-list-chat>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						img: "img",
						article_id: "article_id",
						title: "title",
						description: "description",
						title: "title",
						create_time: "create_time",
						praise: "praise",
						see: "see"
					}
				}
			}
		}
	}
</script>

<style scoped>
	.diy_article .chat-custom-right {
		font-size: 0.875rem;
		line-height: 1.5;
		text-align: right;
		color: #777;
	}
	
	.diy_article .praise {
		position: relative;
		top: 0.125rem;
	}
	
	.diy_article .praise::before {
		content: "";
		display: inline-block;
		width: 1rem;
		height: 1rem;
		background: url('/static/img/praise.png') 100% 100% no-repeat;
		position: relative;
		top: 0.125rem;
		margin-right: 0.25rem;
	}
	
	.diy_article .see {
		margin-left: 1rem;
		position: relative;
		top: 0.125rem;
	}
	
	.diy_article .see::before {
		content: "";
		display: inline-block;
		width: 1rem;
		height: 1rem;
		background: url('/static/img/see.png') 100% 100% no-repeat;
		position: relative;
		top: 0.125rem;
		margin-right: 0.25rem;
	}
	
	.diy_article .time {
		color: #999;
	}
</style>
